/* Reset và cài đặt chung */
:root {
  --primary: #6200EE;
  --primary-light: #8F3BFF;
  --primary-dark: #4B01D0;
  --secondary: #03DAC5;
  --secondary-light: #66FFF8;
  --secondary-dark: #018786;
  --accent: #FF4081;
  --accent-dark: #C51162;
  --success: #00B894;
  --warning: #FDCB6E;
  --danger: #FF4757;
  --background-dark: #121212;
  --background-dark-light: #181818;
  --background-dark-lighter: #222222;
  --background-elevation-1: #1E1E1E;
  --background-elevation-2: #242424;
  --background-elevation-3: #282828;
  --card-bg: rgba(255, 255, 255, 0.05);
  --text-light: #FFFFFF;
  --text-light-secondary: rgba(255, 255, 255, 0.7);
  --text-light-muted: rgba(255, 255, 255, 0.5);
  --border-dark: rgba(255, 255, 255, 0.1);
  --shadow-dark: 0 10px 20px rgba(0, 0, 0, 0.5);
  --shadow-soft: 0 4px 20px rgba(0, 0, 0, 0.3);
  --shadow-button: 0 4px 12px rgba(98, 0, 238, 0.4);
  --transition-speed: 0.3s;
  --border-radius: 16px;
  --border-radius-sm: 8px;
  --border-radius-lg: 24px;
  --font-main: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  --font-headings: 'Space Grotesk', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  --spacing: 8px;
  --header-height: 64px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-main);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--background-dark);
  color: var(--text-light);
  line-height: 1.6;
  overflow-x: hidden;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 calc(var(--spacing) * 3);
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  padding: calc(var(--spacing) * 8) 0;
}

a {
  color: var(--secondary);
  text-decoration: none;
  transition: all var(--transition-speed);
}

a:hover {
  color: var(--secondary-light);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-headings);
  font-weight: 600;
  margin-bottom: calc(var(--spacing) * 2);
}

/* Header & Navigation */
header {
  background-color: rgba(18, 18, 18, 0.85);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: var(--shadow-soft);
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  height: var(--header-height);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--border-dark);
  transition: all 0.3s ease;
}

header.scrolled {
  background-color: rgba(18, 18, 18, 0.95);
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  height: 56px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.navbar-center {
  flex: 1;
  display: flex;
  justify-content: center;
}

.navbar-right {
  display: flex;
  align-items: center;
  gap: calc(var(--spacing) * 3);
}

.brand {
  font-family: var(--font-headings);
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-light);
  margin-right: 2rem;
  position: relative;
  display: inline-block;
  letter-spacing: -0.5px;
  transition: all 0.3s ease;
}

.brand:hover {
  transform: translateY(-2px);
}

.brand .gradient-text {
  margin-right: 4px;
}

.nav-links {
  display: flex;
  list-style: none;
  align-items: center;
  gap: calc(var(--spacing) * 4);
}

.nav-links a, .nav-links .nav-button {
  color: var(--text-light);
  font-weight: 500;
  transition: all var(--transition-speed);
  position: relative;
  padding: calc(var(--spacing)) calc(var(--spacing) * 1.5);
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 8px;
  border-radius: var(--border-radius-sm);
}

.nav-links a.active {
  color: var(--secondary);
  background-color: rgba(3, 218, 197, 0.1);
}

.nav-links a:hover, .nav-links .nav-button:hover {
  color: var(--secondary);
  background-color: rgba(3, 218, 197, 0.05);
}

.nav-links a i, .nav-links .nav-button i {
  font-size: 0.9rem;
}

.nav-button {
  background: none;
  border: none;
  cursor: pointer;
  font-family: inherit;
  font-size: 0.95rem;
  font-weight: 500;
  padding: calc(var(--spacing)) calc(var(--spacing) * 1.5);
  color: var(--text-light);
}

.nav-button-highlighted {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white !important;
  padding: calc(var(--spacing)) calc(var(--spacing) * 2) !important;
  border-radius: var(--border-radius-sm);
  box-shadow: var(--shadow-button);
  transition: all 0.3s ease !important;
}

.nav-button-highlighted:hover {
  background: linear-gradient(135deg, var(--primary-light), var(--primary)) !important;
  color: white !important;
  transform: translateY(-2px);
  box-shadow: 0 6px 15px rgba(98, 0, 238, 0.5) !important;
}

/* Menu toggle for mobile */
.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 28px;
  height: 20px;
  cursor: pointer;
  z-index: 1001;
}

.menu-toggle span {
  width: 100%;
  height: 2px;
  background-color: var(--text-light);
  border-radius: 2px;
  transition: all 0.3s ease;
}

.menu-toggle.active span:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}

.menu-toggle.active span:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active span:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
}

/* Scroll indicator */
.scroll-progress {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  z-index: 1000;
  overflow: hidden;
}

.scroll-progress-bar {
  height: 100%;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  width: 0%;
  transition: width 0.1s ease-out;
}

/* Home Page */
.home-container {
  padding-top: calc(var(--header-height) + var(--spacing) * 2);
}

.home-header {
  margin-bottom: calc(var(--spacing) * 8);
  text-align: center;
  position: relative;
}

.home-title {
  font-family: var(--font-headings);
  font-size: 3.5rem;
  margin-bottom: calc(var(--spacing) * 2);
  background: linear-gradient(120deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -1px;
  position: relative;
  display: inline-block;
}

.home-title::after {
  content: '';
  position: absolute;
  width: 40%;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  bottom: -10px;
  left: 30%;
  border-radius: 2px;
}

.home-subtitle {
  color: var(--text-light-secondary);
  font-size: 1.1rem;
  max-width: 600px;
  margin: 0 auto;
}

.featured-post {
  margin-bottom: calc(var(--spacing) * 8);
}

.featured-post-card {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: calc(var(--spacing) * 4);
  background-color: var(--background-elevation-1);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow-soft);
  transition: transform var(--transition-speed), box-shadow var(--transition-speed);
  border: 1px solid var(--border-dark);
}

.featured-post-card:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-dark);
}

.featured-post-image {
  height: 100%;
  overflow: hidden;
}

.featured-post-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.featured-post-card:hover .featured-post-image img {
  transform: scale(1.05);
}

.featured-post-content {
  padding: calc(var(--spacing) * 4);
  display: flex;
  flex-direction: column;
}

.featured-label {
  display: inline-block;
  background-color: rgba(98, 0, 238, 0.1);
  color: var(--primary);
  padding: 4px 10px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: calc(var(--spacing) * 2);
}

.featured-post-title {
  font-size: 1.8rem;
  margin-bottom: calc(var(--spacing) * 3);
  line-height: 1.2;
}

.featured-post-title a {
  color: var(--text-light);
}

.featured-post-title a:hover {
  color: var(--secondary);
}

.featured-post-excerpt {
  color: var(--text-light-secondary);
  margin-bottom: calc(var(--spacing) * 3);
  flex-grow: 1;
}

.posts-section-title {
  font-family: var(--font-headings);
  position: relative;
  margin-bottom: calc(var(--spacing) * 6);
  padding-bottom: calc(var(--spacing) * 2);
  font-size: 2rem;
}

.posts-section-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  border-radius: 2px;
}

.post-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
  gap: calc(var(--spacing) * 4);
}

.post-card {
  background-color: var(--background-elevation-1);
  border-radius: var(--border-radius);
  overflow: hidden;
  box-shadow: var(--shadow-soft);
  transition: all var(--transition-speed);
  border: 1px solid var(--border-dark);
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.post-card:hover {
  transform: translateY(-8px) scale(1.01);
  box-shadow: var(--shadow-dark);
  border-color: rgba(98, 0, 238, 0.2);
}

.post-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(98, 0, 238, 0.2), rgba(3, 218, 197, 0.1));
  opacity: 0;
  z-index: 0;
  transition: opacity var(--transition-speed);
}

.post-card:hover::before {
  opacity: 1;
}

.post-card-image {
  position: relative;
  height: 220px;
  overflow: hidden;
}

.post-card-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.post-card:hover .post-card-image img {
  transform: scale(1.08);
}

.post-card-content {
  padding: calc(var(--spacing) * 3);
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
}

.post-card-category {
  position: absolute;
  top: -15px;
  right: 20px;
  background-color: var(--primary);
  color: white;
  font-size: 0.8rem;
  padding: 4px 12px;
  border-radius: 20px;
  font-weight: 500;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.post-card h2 {
  font-size: 1.35rem;
  margin-bottom: calc(var(--spacing) * 2);
  line-height: 1.4;
}

.post-card h2 a {
  color: var(--text-light);
  transition: color var(--transition-speed);
}

.post-card h2 a:hover {
  color: var(--secondary);
}

.post-card-excerpt {
  color: var(--text-light-secondary);
  margin-bottom: calc(var(--spacing) * 3);
  flex-grow: 1;
  font-size: 0.95rem;
  line-height: 1.6;
}

.post-card-meta {
  display: flex;
  justify-content: space-between;
  font-size: 0.85rem;
  color: var(--text-light-muted);
  padding-top: calc(var(--spacing) * 2);
  border-top: 1px solid var(--border-dark);
}

.post-card-meta span {
  display: flex;
  align-items: center;
}

.post-card-meta i {
  margin-right: 5px;
  font-size: 0.9rem;
  color: var(--secondary);
}

.post-card-actions {
  margin-top: calc(var(--spacing) * 3);
}

/* Post Detail */
.post-detail {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2.5rem;
  margin-bottom: 2.5rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.post-header {
  margin-bottom: 2rem;
}

.post-title {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  line-height: 1.3;
}

.post-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--text-light-muted);
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid var(--border-dark);
}

.post-meta-author {
  display: flex;
  align-items: center;
}

.author-avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.post-meta-info {
  display: flex;
  gap: 1rem;
}

.post-meta-info span {
  display: flex;
  align-items: center;
}

.post-meta-info svg {
  margin-right: 5px;
}

.post-image {
  width: 100%;
  max-height: 500px;
  object-fit: cover;
  border-radius: var(--border-radius-sm);
  margin-bottom: 2rem;
}

.post-content {
  line-height: 1.8;
  margin-bottom: 2.5rem;
  font-size: 1.05rem;
}

.post-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 2rem;
}

.post-tag {
  background-color: rgba(124, 77, 255, 0.1);
  color: var(--primary-light);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.85rem;
}

/* Comments */
.comment-section {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.comment-section-title {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-dark);
}

.comment-form {
  margin-bottom: 2.5rem;
}

.comment-list {
  margin-top: 2rem;
}

.comment {
  background-color: var(--background-dark-lighter);
  border-radius: var(--border-radius-sm);
  padding: 1.2rem;
  margin-bottom: 1.2rem;
}

.comment-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.8rem;
}

.comment-author {
  font-weight: 600;
  display: flex;
  align-items: center;
}

.comment-author-avatar {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
  background-color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: bold;
}

.comment-date {
  color: var(--text-light-muted);
  font-size: 0.85rem;
}

.comment-content {
  line-height: 1.6;
}

/* Forms */
.form-container {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2.5rem;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.form-title {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 2rem;
}

.form-group {
  margin-bottom: 1.8rem;
}

label {
  display: block;
  margin-bottom: 0.6rem;
  font-weight: 500;
}

input, textarea, select {
  width: 100%;
  padding: 0.9rem 1.2rem;
  border: 1px solid var(--border-dark);
  border-radius: var(--border-radius-sm);
  background-color: var(--background-dark-lighter);
  color: var(--text-light);
  transition: all var(--transition-speed);
  font-family: inherit;
  font-size: 1rem;
}

input:focus, textarea:focus, select:focus {
  border-color: var(--primary);
  outline: none;
  box-shadow: 0 0 0 3px rgba(124, 77, 255, 0.2);
}

textarea {
  min-height: 150px;
  resize: vertical;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.8rem 1.6rem;
  border-radius: var(--border-radius-sm);
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  transition: all var(--transition-speed);
  border: none;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
}

.btn svg {
  margin-right: 8px;
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  box-shadow: 0 4px 10px rgba(124, 77, 255, 0.3);
}

.btn-primary:hover {
  background: linear-gradient(135deg, var(--primary-light), var(--primary));
  box-shadow: 0 6px 15px rgba(124, 77, 255, 0.4);
  transform: translateY(-2px);
}

.btn-secondary {
  background-color: transparent;
  color: var(--text-light);
  border: 1px solid var(--border-dark);
}

.btn-secondary:hover {
  background-color: var(--background-dark-lighter);
  border-color: var(--primary);
}

.btn-danger {
  background: linear-gradient(135deg, var(--danger), #B71C1C);
  color: white;
  box-shadow: 0 4px 10px rgba(255, 23, 68, 0.3);
}

.btn-danger:hover {
  background: linear-gradient(135deg, #FF5252, var(--danger));
  box-shadow: 0 6px 15px rgba(255, 23, 68, 0.4);
  transform: translateY(-2px);
}

.btn-sm {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}

.btn-lg {
  padding: 1rem 2rem;
  font-size: 1.1rem;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  margin-top: 2.5rem;
  gap: 1rem;
}

/* Admin Panel */
.admin-container {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 2rem;
}

.admin-sidebar {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 1.8rem;
  box-shadow: var(--shadow-soft);
  height: max-content;
  border: 1px solid var(--border-dark);
}

.admin-menu {
  list-style: none;
}

.admin-menu li {
  margin-bottom: 0.8rem;
}

.admin-menu a {
  display: block;
  padding: 0.8rem 1.2rem;
  border-radius: var(--border-radius-sm);
  color: var(--text-light);
  transition: all var(--transition-speed);
}

.admin-menu a:hover, .admin-menu a.active {
  background-color: var(--background-dark-lighter);
  color: var(--primary);
}

.admin-content {
  background-color: var(--card-bg);
  border-radius: var(--border-radius);
  padding: 2rem;
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-dark);
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid var(--border-dark);
}

/* Tables */
table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-top: 1.5rem;
  border-radius: var(--border-radius-sm);
  overflow: hidden;
}

th, td {
  padding: 1.2rem;
  text-align: left;
}

th {
  font-weight: 600;
  background-color: var(--background-dark-lighter);
  color: var(--text-light);
}

td {
  border-bottom: 1px solid var(--border-dark);
}

tbody tr {
  transition: background-color var(--transition-speed);
}

tbody tr:hover {
  background-color: rgba(124, 77, 255, 0.05);
}

/* Messages */
.error-message {
  background-color: rgba(255, 23, 68, 0.15);
  color: #FF8A80;
  padding: 1.2rem;
  border-radius: var(--border-radius-sm);
  margin-bottom: 1.8rem;
  border-left: 4px solid var(--danger);
}

.success-message {
  background-color: rgba(0, 200, 83, 0.15);
  color: #69F0AE;
  padding: 1.2rem;
  border-radius: var(--border-radius-sm);
  margin-bottom: 1.8rem;
  border-left: 4px solid var(--success);
}

/* Footer */
footer {
  background-color: var(--background-dark);
  color: var(--text-light-secondary);
  position: relative;
  margin-top: calc(var(--spacing) * 6);
  border-top: 1px solid var(--border-dark);
}

.footer-wave {
  position: absolute;
  top: -120px;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
  transform: rotate(180deg);
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: calc(var(--spacing) * 5);
  padding: calc(var(--spacing) * 8) 0 calc(var(--spacing) * 5);
}

.footer-section {
  display: flex;
  flex-direction: column;
  gap: calc(var(--spacing) * 2);
}

.footer-logo {
  font-family: var(--font-headings);
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: calc(var(--spacing));
  letter-spacing: -0.5px;
}

.gradient-text {
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  display: inline-block;
}

.footer-description {
  color: var(--text-light-muted);
  line-height: 1.6;
  margin-bottom: calc(var(--spacing) * 2);
}

.footer-section h3 {
  font-size: 1.2rem;
  margin-bottom: calc(var(--spacing) * 3);
  position: relative;
  display: inline-block;
  padding-bottom: calc(var(--spacing));
}

.footer-section h3::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 40px;
  height: 2px;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  border-radius: 2px;
}

.footer-links {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: calc(var(--spacing) * 1.5);
}

.footer-links li {
  transition: transform var(--transition-speed);
}

.footer-links li:hover {
  transform: translateX(5px);
}

.footer-links a {
  color: var(--text-light-secondary);
  transition: all var(--transition-speed);
  display: inline-flex;
  align-items: center;
}

.footer-links a::before {
  content: '→';
  margin-right: calc(var(--spacing));
  color: var(--secondary);
  font-size: 0.9rem;
  opacity: 0;
  transform: translateX(-10px);
  transition: all var(--transition-speed);
}

.footer-links a:hover {
  color: var(--secondary);
}

.footer-links a:hover::before {
  opacity: 1;
  transform: translateX(0);
}

.newsletter-form {
  display: flex;
  margin-top: calc(var(--spacing) * 2);
}

.newsletter-form input {
  flex: 1;
  background: var(--background-dark-lighter);
  border: 1px solid var(--border-dark);
  padding: calc(var(--spacing) * 1.5);
  border-radius: var(--border-radius-sm) 0 0 var(--border-radius-sm);
  color: var(--text-light);
}

.btn-subscribe {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  border: none;
  padding: 0 calc(var(--spacing) * 2);
  border-radius: 0 var(--border-radius-sm) var(--border-radius-sm) 0;
  cursor: pointer;
  transition: all var(--transition-speed);
}

.btn-subscribe:hover {
  background: linear-gradient(135deg, var(--primary-light), var(--primary));
}

.social-links {
  display: flex;
  gap: calc(var(--spacing) * 2);
  margin-top: calc(var(--spacing));
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  background: var(--background-dark-lighter);
  color: var(--text-light-secondary);
  border-radius: 50%;
  transition: all var(--transition-speed);
  border: 1px solid var(--border-dark);
  text-decoration: none;
}

.social-link:hover {
  transform: translateY(-5px);
  color: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.social-link:nth-child(1):hover {
  background: #1877f2; /* Facebook color */
}

.social-link:nth-child(2):hover {
  background: #1da1f2; /* Twitter color */
}

.social-link:nth-child(3):hover {
  background: #e1306c; /* Instagram color */
}

.social-link:nth-child(4):hover {
  background: #0077b5; /* LinkedIn color */
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: calc(var(--spacing) * 3) 0;
  border-top: 1px solid var(--border-dark);
}

.footer-copyright {
  font-size: 0.9rem;
  color: var(--text-light-muted);
}

.footer-back-to-top {
  display: inline-block;
}

.footer-back-to-top a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: var(--background-elevation-1);
  color: var(--secondary);
  border-radius: 50%;
  transition: all var(--transition-speed);
  border: 1px solid var(--border-dark);
}

.footer-back-to-top a:hover {
  background: var(--secondary);
  color: var(--background-dark);
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(3, 218, 197, 0.3);
}

/* Responsive */
@media (max-width: 768px) {
  .post-grid {
    grid-template-columns: 1fr;
  }

  .admin-container {
    grid-template-columns: 1fr;
  }

  .navbar {
    flex-direction: column;
  }

  .navbar-center {
    justify-content: flex-end;
  }

  .navbar-right {
    gap: calc(var(--spacing));
  }

  .brand {
    margin-bottom: 1rem;
  }

  .nav-links {
    position: fixed;
    top: 0;
    right: -100%;
    width: 80%;
    max-width: 300px;
    height: 100vh;
    background-color: var(--background-dark);
    flex-direction: column;
    align-items: flex-start;
    padding: calc(var(--header-height) + var(--spacing) * 2) var(--spacing) * 3;
    gap: calc(var(--spacing) * 2);
    transition: all 0.3s ease;
    box-shadow: -5px 0 15px rgba(0, 0, 0, 0.3);
    z-index: 1000;
  }

  .nav-links.active {
    right: 0;
  }

  .nav-links li {
    width: 100%;
  }

  .nav-links a, .nav-links .nav-button {
    width: 100%;
    padding: calc(var(--spacing) * 1.5);
    border-radius: var(--border-radius-sm);
  }

  .form-actions {
    flex-direction: column;
  }

  .form-actions button {
    width: 100%;
  }

  .post-title {
    font-size: 2rem;
  }

  .post-detail {
    padding: 1.5rem;
  }

  .footer-content {
    flex-direction: column;
  }
}

/* Image Preview */
.image-preview {
  margin-top: 1rem;
  border-radius: var(--border-radius-sm);
  overflow: hidden;
  border: 1px solid var(--border-dark);
}

.image-preview img {
  max-width: 100%;
  max-height: 250px;
  display: block;
}

/* Ratings */
.rating-container {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
}

.rating-label {
  margin-right: 1rem;
  font-weight: 500;
}

.stars {
  display: flex;
  gap: 0.3rem;
}

.star {
  font-size: 1.8rem;
  cursor: pointer;
  color: var(--text-light-muted);
  transition: all 0.2s ease;
}

.star:hover {
  transform: scale(1.2);
}

.star.filled {
  color: #FFD700;
}

/* Not Found */
.not-found-container {
  text-align: center;
  padding: 5rem 2rem;
  max-width: 700px;
  margin: 0 auto;
}

.not-found-container h1 {
  font-size: 8rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(90deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.not-found-container h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
}

.not-found-container p {
  margin-bottom: 2.5rem;
  color: var(--text-light-muted);
  font-size: 1.1rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  animation: fadeIn 0.5s ease forwards;
}

/* Loading Spinner */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(124, 77, 255, 0.3);
  border-radius: 50%;
  border-top-color: var(--primary);
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Placeholder Image */
.placeholder-image {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.5rem;
}

/* Scroll to Top Button */
.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  border: none;
  box-shadow: 0 4px 15px rgba(98, 0, 238, 0.4);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.scroll-to-top.visible {
  opacity: 1;
  transform: translateY(0);
}

.scroll-to-top:hover {
  background: linear-gradient(135deg, var(--primary-light), var(--primary));
  box-shadow: 0 6px 20px rgba(98, 0, 238, 0.6);
  transform: translateY(-5px);
}

.scroll-to-top:active {
  transform: translateY(0);
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(98, 0, 238, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(98, 0, 238, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(98, 0, 238, 0);
  }
}

.scroll-to-top.pulse {
  animation: pulse 1.5s infinite;
}

/* Search Bar */
.search-bar {
  position: relative;
  max-width: 300px;
  margin-left: auto;
}

.search-bar form {
  display: flex;
  align-items: center;
  position: relative;
}

.search-input {
  background-color: var(--background-dark-lighter);
  border: 1px solid var(--border-dark);
  border-radius: var(--border-radius);
  padding: calc(var(--spacing) * 1.5) calc(var(--spacing) * 4) calc(var(--spacing) * 1.5) calc(var(--spacing) * 2);
  color: var(--text-light);
  width: 100%;
  transition: all var(--transition-speed);
  font-size: 0.9rem;
}

.search-input:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(98, 0, 238, 0.2);
  outline: none;
}

.search-toggle, .search-submit {
  position: absolute;
  background: none;
  border: none;
  color: var(--text-light-secondary);
  cursor: pointer;
  transition: all var(--transition-speed);
  padding: calc(var(--spacing) * 0.5);
  font-size: 0.9rem;
}

.search-toggle {
  right: calc(var(--spacing) * 2);
}

.search-submit {
  right: calc(var(--spacing) * 0.5);
  opacity: 0;
  visibility: hidden;
  color: var(--secondary);
  transform: translateX(10px);
}

.search-bar.expanded .search-submit {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}

.search-input::placeholder {
  color: var(--text-light-muted);
}

/* Mobile Search Bar */
@media (max-width: 768px) {
  .search-bar {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    max-width: 100%;
    padding: 0 calc(var(--spacing) * 3);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-10px);
    transition: all var(--transition-speed);
    z-index: 999;
  }

  .search-bar.expanded {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }

  .search-input {
    background-color: var(--background-dark-light);
    border-radius: 0;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
  }
}

/* Search Results Page */
.search-results-container {
  padding-top: calc(var(--header-height) + var(--spacing) * 2);
}

.search-header {
  margin-bottom: calc(var(--spacing) * 6);
  text-align: center;
}

.search-title {
  font-size: 2.5rem;
  margin-bottom: calc(var(--spacing) * 2);
  background: linear-gradient(120deg, var(--primary), var(--secondary));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  letter-spacing: -1px;
}

.search-subtitle {
  color: var(--text-light-secondary);
  font-size: 1.1rem;
}

.search-results {
  display: flex;
  flex-direction: column;
  gap: calc(var(--spacing) * 3);
}

.search-result-item {
  background-color: var(--background-elevation-1);
  border-radius: var(--border-radius);
  padding: calc(var(--spacing) * 3);
  border: 1px solid var(--border-dark);
  box-shadow: var(--shadow-soft);
  transition: all var(--transition-speed);
  display: flex;
  gap: calc(var(--spacing) * 3);
}

.search-result-item:hover {
  transform: translateY(-4px);
  border-color: rgba(3, 218, 197, 0.3);
  box-shadow: var(--shadow-dark);
}

.search-result-content {
  flex: 1;
}

.search-result-image {
  flex: 0 0 200px;
  height: 150px;
  overflow: hidden;
  border-radius: var(--border-radius-sm);
}

.search-result-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}

.search-result-item:hover .search-result-image img {
  transform: scale(1.05);
}

.search-result-item h2 {
  font-size: 1.5rem;
  margin-bottom: calc(var(--spacing) * 2);
}

.search-result-item h2 a {
  color: var(--text-light);
  transition: color var(--transition-speed);
}

.search-result-item h2 a:hover {
  color: var(--secondary);
}

.search-result-excerpt {
  color: var(--text-light-secondary);
  margin-bottom: calc(var(--spacing) * 3);
  line-height: 1.6;
}

.search-result-meta {
  display: flex;
  gap: calc(var(--spacing) * 3);
  color: var(--text-light-muted);
  font-size: 0.9rem;
  margin-top: auto;
}

.search-result-meta span {
  display: flex;
  align-items: center;
  gap: calc(var(--spacing) * 0.5);
}

.search-result-meta i {
  color: var(--secondary);
}

.search-result-category {
  background-color: rgba(98, 0, 238, 0.1);
  color: var(--primary-light);
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
}

.no-results {
  text-align: center;
  padding: calc(var(--spacing) * 6) 0;
  color: var(--text-light-secondary);
}

.no-results-icon {
  font-size: 4rem;
  color: var(--text-light-muted);
  margin-bottom: calc(var(--spacing) * 3);
}

.no-results p {
  margin-bottom: calc(var(--spacing) * 2);
  font-size: 1.1rem;
}

mark {
  background-color: rgba(98, 0, 238, 0.2);
  color: var(--secondary-light);
  padding: 0 3px;
  border-radius: 3px;
}

@media (max-width: 768px) {
  .search-result-item {
    flex-direction: column;
  }

  .search-result-image {
    flex: 0 0 auto;
    height: 200px;
    order: -1;
    margin-bottom: calc(var(--spacing) * 2);
  }
}
